@import url("https://fonts.googleapis.com/css?family=Roboto|Montserrat|Lato|Open+Sans|Open+Sans+Condensed:300&display=swap");
* {
  margin: 0;
  padding: 0; }

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }
  body .MuiInputBase-root, body .MuiTypography-root {
    color: var(--color-body-invert); }
  body .MuiInputLabel-root {
    color: var(--color-body-invert-dim); }

.body {
  font-family: 'Open Sans', sans-serif;
  background-color: var(--color-body);
  color: var(--color-body-invert); }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

:root {
  --height-nav: 50px;
  --color-body: #fcfcfc;
  --color-body-dim: #E6E6E6;
  --color-body-invert: #252525;
  --color-body-invert-dim: #6E6E6E;
  --color-primary: #05a081;
  --color-primary-alt: #1BA88C;
  --color-secondary: #114B5F;
  --color-secondary-alt: #265B6D;
  --color-default: #232a34;
  --color-default-alt: #373D46;
  --color-white: white;
  --color-black: black;
  --color-success: #437A34;
  --color-warning: #49A078;
  --color-failure: #8C030E;
  --color-nav: #232a34;
  --color-nav-shadow: rgba(252,252,252,.15);
  --color-nav-text: var(--color-white);
  --color-nav-hover: rgba(145, 204, 217, 0.1);
  --color-nav-active: var(--color-secondary);
  --color-nav-slider: #373D46;
  --color-navitem-mobile: var(--color-white);
  --color-btn-primary-bg: var(--color-primary);
  --color-btn-primary-bg-hover: var(--color-primary-alt);
  --color-btn-secondary-bg: var(--color-secondary);
  --color-btn-secondary-bg-hover: var(--color-secondary-alt);
  --color-btn-default-bg: var(--color-default);
  --color-btn-default-bg-hover: var(--color-default-alt);
  --color-btn-invert-bg: var(--color-body);
  --color-btn-invert-bg-hover: var(--color-body-dim);
  --color-btn-fg-dark: black;
  --color-btn-fg-light: white;
  --color-btn-action-danger-bg: #B33951;
  --color-dialog: var(--color-body); }
